export default "import { Component, OnInit } from \"@angular/core\";\nimport { Chart, CHART_PALETTE_CS2, IChartSeries, ILineAccessors, LineAccessors, LinearScale, LineRenderer, XYGrid } from \"@nova-ui/charts\";\n\n@Component({\n    selector: \"nui-colors-custom-accessor-example\",\n    templateUrl: \"./colors-custom-accessor.example.component.html\",\n})\nexport class RendererColorsCustomAccessorExampleComponent implements OnInit {\n    public chart = new Chart(new XYGrid());\n\n    public ngOnInit() {\n        const scales = {\n            x: new LinearScale(),\n            y: new LinearScale(),\n        };\n        const renderer = new LineRenderer();\n        const accessors: ILineAccessors = new LineAccessors();\n        // Custom color accessor\n        accessors.series.color = () => CHART_PALETTE_CS2[6];\n\n        const seriesSet: IChartSeries<ILineAccessors>[] = getData().map(d => ({\n            ...d,\n            scales,\n            renderer,\n            accessors,\n        }));\n\n        this.chart.update(seriesSet);\n    }\n}\n\n/* Chart data */\nfunction getData() {\n    return [\n        {\n            id: \"series-1\",\n            name: \"Series 1\",\n            data: [\n                { x: 1, y: 30 },\n                { x: 2, y: 50 },\n                { x: 3, y: 25 },\n                { x: 4, y: 40 },\n                { x: 5, y: 35 },\n            ],\n        },\n        {\n            id: \"series-2\",\n            name: \"Series 2\",\n            data: [\n                { x: 1, y: 20 },\n                { x: 2, y: 40 },\n                { x: 3, y: 15 },\n                { x: 4, y: 30 },\n                { x: 5, y: 25 },\n            ],\n        },\n        {\n            id: \"series-3\",\n            name: \"Series 3\",\n            data: [\n                { x: 1, y: 10 },\n                { x: 2, y: 30 },\n                { x: 3, y: 5 },\n                { x: 4, y: 20 },\n                { x: 5, y: 15 },\n            ],\n        },\n    ];\n}\n";