export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule, Routes } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiDocsModule, SrlcStage } from \"@nova-ui/bits\";\nimport { NuiChartsModule } from \"@nova-ui/charts\";\n\nimport { ChartDocsScalesComponent } from \"./chart-docs-scales.component\";\n\nconst exampleRoutes: Routes = [\n    {\n        path: \"\",\n        component: ChartDocsScalesComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.beta,\n            },\n        },\n    },\n    {\n        path: \"domains\",\n        loadChildren: async () => import(\"components/demo/advanced-usage/scales/domains/chart-docs-domains.module\").then(m => m.ChartDocsDomainsModule),\n    },\n    {\n        path: \"formatters\",\n        loadChildren: async () => import(\"components/demo/advanced-usage/scales/formatters/chart-docs-formatters.module\")\n            .then(m => m.ChartDocsFormattersModule),\n    },\n];\n\n@NgModule({\n    declarations: [\n        ChartDocsScalesComponent,\n    ],\n    imports: [\n        NuiChartsModule,\n        NuiDocsModule,\n        RouterModule.forChild(exampleRoutes),\n    ],\n    providers: [\n        { provide: DEMO_PATH_TOKEN, useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/) },\n    ],\n})\nexport class ChartDocsScalesModule {\n}\n";