export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule, Routes } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiDocsModule, NuiMessageModule, SrlcStage } from \"@nova-ui/bits\";\nimport { NuiChartsModule } from \"@nova-ui/charts\";\n\nimport { ChartDocsFormattersComponent } from \"./chart-docs-formatters.component\";\nimport { TickFormatterExampleComponent } from \"./tick-formatter/tick-formatter.example.component\";\n\nconst exampleRoutes: Routes = [\n    {\n        path: \"\",\n        component: ChartDocsFormattersComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.beta,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"tick\",\n        component: TickFormatterExampleComponent,\n    },\n];\n\n@NgModule({\n    declarations: [\n        TickFormatterExampleComponent,\n        ChartDocsFormattersComponent,\n    ],\n    imports: [\n        NuiChartsModule,\n        NuiDocsModule,\n        NuiMessageModule,\n        RouterModule.forChild(exampleRoutes),\n    ],\n    providers: [\n        { provide: DEMO_PATH_TOKEN, useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/) },\n    ],\n})\nexport class ChartDocsFormattersModule {\n}\n";