export default "import { Component, OnInit } from \"@angular/core\";\nimport { Chart, IChartSeries, ILineAccessors, LineAccessors, LinearScale, LineRenderer, XYGrid } from \"@nova-ui/charts\";\n\n@Component({\n    selector: \"nui-line-chart-example\",\n    templateUrl: \"./line-chart.example.component.html\",\n})\nexport class LineChartExampleComponent implements OnInit {\n    public chart = new Chart(new XYGrid());\n    public seriesSet: IChartSeries<ILineAccessors>[];\n\n    public ngOnInit() {\n        this.seriesSet = [{\n            id: \"series-1\",\n            name: \"Series 1\",\n            data: [\n                { x: 1, y: 10 },\n                { x: 2, y: 30 },\n                { x: 3, y: 5 },\n                { x: 4, y: 20 },\n                { x: 5, y: 15 },\n            ],\n            scales: {\n                x: new LinearScale(),\n                y: new LinearScale(),\n            },\n            renderer: new LineRenderer(),\n            accessors: new LineAccessors(),\n        }];\n\n        this.chart.update(this.seriesSet);\n    }\n}\n";