export default "import { Component } from \"@angular/core\";\nimport {\n    Chart, ChartAssist, ChartPalette, CHART_MARKERS, CHART_PALETTE_CS1, IChartMarker, IChartPalette, IValueProvider, LineAccessors, LineRenderer,\n    SequentialChartMarkerProvider, XYGrid,\n} from \"@nova-ui/charts\";\n\n@Component({\n    selector: \"nui-legend-test\",\n    templateUrl: \"./legend-test.component.html\",\n})\nexport class LegendTestExampleComponent {\n    public palette: IChartPalette = new ChartPalette(CHART_PALETTE_CS1);\n    public markers: IValueProvider<IChartMarker> = new SequentialChartMarkerProvider(CHART_MARKERS);\n    public chart = new Chart(new XYGrid());\n    public chartAssist: ChartAssist = new ChartAssist(this.chart);\n\n    public seriesData = [\n        {\n            seriesId: \"1\",\n            value: 15.5,\n            unitLabel: \"Kbps\",\n            descriptionPrimary: \"Primary Description 1\",\n            descriptionSecondary: \"Secondary Description 1\",\n            status: \"critical\",\n        },\n        {\n            seriesId: \"2\",\n            value: 20.8,\n            unitLabel: \"%\",\n            descriptionPrimary: \"Primary Description 2\",\n            descriptionSecondary: \"Secondary Description 2\",\n            status: \"warning\",\n        },\n    ];\n\n    constructor() {\n        const accessors = new LineAccessors();\n        const renderer = new LineRenderer();\n        this.chartAssist.update(this.seriesData.map(series => ({\n            id: series.seriesId,\n            data: [series.value],\n            accessors,\n            renderer,\n            scales: {},\n        })));\n    }\n}\n";