export default "import { Component, OnInit } from \"@angular/core\";\nimport { Chart, IChartSeries, ILineAccessors, LineAccessors, LinearScale, LineRenderer, XYGrid, XYGridConfig } from \"@nova-ui/charts\";\n\n@Component({\n    selector: \"nui-base-grid-margins-example\",\n    templateUrl: \"./base-grid-margins.example.component.html\",\n})\nexport class BaseGridMarginsExampleComponent implements OnInit {\n    public chart: Chart;\n\n    public ngOnInit() {\n        const gridConfig = new XYGridConfig();\n        gridConfig.dimension.margin = {\n            top: 30,\n            bottom: 30,\n            left: 100,\n            right: 100,\n        };\n\n        this.chart = new Chart(new XYGrid(gridConfig));\n        const seriesSet: IChartSeries<ILineAccessors>[] = getData().map(d => ({\n            ...d,\n            accessors: new LineAccessors(),\n            renderer: new LineRenderer(),\n            scales: {\n                x: new LinearScale(),\n                y: new LinearScale(),\n            },\n        }));\n\n        this.chart.update(seriesSet);\n    }\n}\n\n/* Chart data */\nfunction getData() {\n    return [{\n        id: \"series-1\",\n        name: \"Series 1\",\n        data: [\n            { x: 1, y: 10 },\n            { x: 2, y: 30 },\n            { x: 3, y: 5 },\n            { x: 4, y: 20 },\n            { x: 5, y: 15 },\n        ],\n    }];\n}\n";